

*{
  box-sizing: border-box;
}

body{
  background-color: #22254b;
  color: white;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin: 0px;
}

.root{
  display: flex;
  flex-direction: column;
}

header{
  background-color: #373b69;
  padding: 1rem;
  display: flex;
  justify-content: flex-end;

}

.search{
  background-color: transparent;
  border: 2px solid #22254b;
  border-radius: 40px;
  color: white;
  font-family: inherit;
  font-size: 1.2rem;
  padding: 0.5rem 1.5rem;
}

.search:focus{
  outline: 0;
}



.movie-container{
  display: flex;
  flex-wrap: wrap;

}

.movie{
  background-color: #373b69;
  border-radius: 3px;
  box-shadow: 3px 3px 3px rgba(0,0,0,0.1);
  margin: 1rem;
  width:  300px;
  overflow: hidden;
  position: relative;

}

.movie img {
  max-width: 100%;
}

.movie-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.movie-info h3{
  margin: 0;
}

.movie-over{
  background-color: white;
  color: #22254b;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(100%);
  transition: .5s;
}

.movie:hover .movie-over{
  transform: translateY(0%);
  transition: .5s;
}
